#playButton {
  color:#000000;
  font-size:26px;
  border: 2px solid #2CA1E3;
  border-radius: 5px;
  margin-top: 10px;
  width:300px;
  height:55px;
  padding: 0;
  box-shadow: 5px 10px 8px #888888;
  margin-left: 4px;
  background-color: white;
}

#switchToMoneyButton {
  color: #000000;
  font-size: 26px;
  border: 2px solid #2CA1E3;
  border-radius: 5px;
  width:300px;
  height:55px;
  padding: 0;
  margin-right: 4px;
  margin-top: 10px;
  box-shadow: 5px 10px 8px #888888;
  background-color: white;
}

#gameHelpButtonStyle {
  left: 5%;
  margin-top: 10px;
  height:30px;
  position:absolute;
  font-size: 18px;
  line-height: 18px;
  padding: 0 5px 0 5px;
  border: 2px solid #2CA1E3;
  border-radius: 5px;
  background-color: white;
}

#lobbyButtonStyle {
  right: 5%;
  margin-top: 10px;
  height:30px;
  position:absolute;
  font-size: 18px;
  line-height: 18px;
  padding: 0px 5px 0 5px;
  border: 2px solid #2CA1E3;
  border-radius: 5px;
  background-color: white;
}

#gameHelpButtonStyle:focus,
#lobbyButtonStyle:focus,
#switchToMoneyButton:focus,
#playButton:focus {
  border-width: 2px;
  background-color: #97D9FF;
}

#hiButton, #loButton, #eqButton {
  color:#000000;
  font-size:26px;
  border: 2px solid #2CA1E3;
  border-radius: 5px;
  margin-top: 10px;
  width:150px;
  height:55px;
  padding: 0;
  box-shadow: 5px 10px 8px #888888;
  margin-left: 4px;
  background-color: white;
}

#hiButton:focus, #loButton:focus, #eqButton:focus {
  border-width: 2px;
  background-color: #97D9FF;
}