.POC-PopupButtons-HD:focus {
  background-color: #47994b;
  border:2px solid #346b37;
}

.POC-Popup {
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.POC-PopupPanelContents {
  margin: 50px auto auto;
  display: inherit;
  border: none;
}

.POC-Popup-Title-HD {
  line-height: 87px;
}

.POC-PopupButtons-Cancel:focus {
  border:2px solid #346b37;
}

.POC-PopupButtons, .POC-PopupButtons-HD,
.POC-PopupButtons-Cancel {
  display: block;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
}