.game-configuration {
  display: table;
  margin: 0 auto;
  text-align: left;
  padding-top:5px;

  .form-group {
    display: table;
    width: 100%;
    height: 34px;

    margin: 0 !important;
  }

  .form-value,
  .control-label {
    display: table-cell;
    padding-right: 10px;
    vertical-align: middle;
    font-size: 26px;
    height: 55px;
    text-align: right;
  }

  .form-control {
    display: table-cell;
    padding-right: 10px;
    vertical-align: middle;
    font-size: 22px;
    height: 40px;
    margin-top: 10px;
  }

  .control-label {
    padding-right: 10px;
    text-align: left;
  }

  .form-control:focus,
  .form-value:focus {
    outline: none;
    border:2px solid #9ecaed;
    border-radius:7px;
    box-shadow: 0 0 -10px #9ecaed;
  }
}

.form-horizontal .control-label {
  text-align: left !important;
}